
import * as THREE from "three";
import { extend } from "@react-three/fiber";
import { SimpleShader } from "./FireShader";
class CloudShaderMaterial extends THREE.ShaderMaterial {
  constructor(texture) {
    super({
		...SimpleShader
      ,
      uniforms: {
        iChannel0: { value: texture },
        iChannel1: { value: texture },
        u_time: { type: "f", value: 0.05 },
        u_speed: { type: "f", value: performance.now() },
        u_mouse: { type: "f", value: { x: 0, y: 0 } },
        u_resolution: {
          type: "v2",
          value: new THREE.Vector2(window.innerWidth, window.innerWidth),
        },
      },
      transparent: true,
      blending: THREE.NormalBlending,
      depthWrite: false,
    });
  }
}

extend({  CloudShaderMaterial });
