import state from "../../../store";
import { useSnapshot } from "valtio";
import GlassButton from "../../../components/GlassButton";
import { transitionViewIfSupported } from "../../../utils";
import { useNavigate } from "react-router-dom";
export default function Nav() {
  const snap = useSnapshot(state);
  const navigate = useNavigate();
  return (
    
    <section className="flex gap-2 justify-between w-full">
    <GlassButton
      onClick={() => {
        state.thought = !snap.thought;
        state.spaceInvasion.pause = !snap.spaceInvasion.pause;
      }}
    >
      {snap.spaceInvasion.pause ? "Play" : "Pause"}
    </GlassButton>
    <GlassButton
      onClick={() => {
        state.spaceInvasion.aliens = [...snap.spaceInvasion.aliens_start];
        state.spaceInvasion.gameOver = false;
        state.spaceInvasion.pause = true;
        state.spaceInvasion.status = null;
        state.spaceInvasion.hits = [];
        state.spaceInvasion.shots = [];
        state.spaceInvasion.player = state.spaceInvasion.player_start;
      }}
    >
      Restart
    </GlassButton>
    <GlassButton
      className="btn_glass_purple_parent"
      onClick={() => {
        transitionViewIfSupported(navigate(-1));
      }}
    >
      Back
    </GlassButton>
  </section>
  );
}
