import * as THREE from "three";
import { extend } from "@react-three/fiber";

class SquarePatternShaderMaterial extends THREE.ShaderMaterial {
  constructor(texture) {
    super({
      vertexShader: `varying vec2 vUv;
      void main() {
        vUv = uv;
        gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
      }`,
      fragmentShader: `#include <common>

      uniform vec3 u_resolution;
      uniform float u_time;
      uniform sampler2D iChannel0;
    
      // By Daedelus: https://www.shadertoy.com/user/Daedelus
      // license: Creative Commons Attribution-NonCommercial-ShareAlike 3.0 Unported License.
      #define TIMESCALE 0.25 
      #define TILES 8
      #define COLOR 0.7, 1.6, 2.8
    
      void mainImage( out vec4 fragColor, in vec2 fragCoord )
      {
        float aspect = u_resolution.y/u_resolution.x;
    float value;
	vec2 uv = fragCoord.xy / u_resolution.x;
    uv -= vec2(0.5, 0.5*aspect);
    float rot = radians(45.0); // radians(45.0*sin(u_time));
    mat2 m = mat2(cos(rot), -sin(rot), sin(rot), cos(rot));
   	uv  = m * uv;
    uv += vec2(0.5, 0.5*aspect);
    uv.y+=0.5*(1.0-aspect);
    vec2 pos = 10.0*uv;
    vec2 rep = fract(pos);
    float dist = 2.0*min(min(rep.x, 1.0-rep.x), min(rep.y, 1.0-rep.y));
    float squareDist = length((floor(pos)+vec2(0.5)) - vec2(5.0) );
    
    float edge = sin(u_time-squareDist*0.5)*0.5+0.5;
    
    edge = (u_time-squareDist*0.5)*0.5;
    edge = 2.0*fract(edge*0.5);
    //value = 2.0*abs(dist-0.5);
    //value = pow(dist, 2.0);
    value = fract (dist*2.0);
    value = mix(value, 1.0-value, step(1.0, edge));
    //value *= 1.0-0.5*edge;
    edge = pow(abs(1.0-edge), 2.0);
    
    //edge = abs(1.0-edge);
    value = smoothstep( edge-0.05, edge, 0.95*value);
    
    
    value += squareDist*.1;
    //fragColor = vec4(value);
    fragColor = mix(vec4(1.0,1.0,1.0,1.0),vec4(1.0,0.25,0.25,1.0), value);
    fragColor.a = 0.25*clamp(value, 0.0, 1.0);
      }
    
      varying vec2 vUv;
    
      void main() {
        mainImage(gl_FragColor, vUv * u_resolution.xy);
      }
      `,
      uniforms: {
        iChannel0: { value: texture },
        u_time: { type: "f", value: 0.05 },
        u_speed: { type: "f", value: performance.now() },
        u_mouse: { type: "f", value: { x: 0, y: 0 } },
        u_resolution: {
          type: "v2",
          value: new THREE.Vector2(window.innerWidth, window.innerWidth),
        },
      },
      transparent: true,
      blending: THREE.NormalBlending,
      depthWrite: false,
    });
  }
}

extend({ SquarePatternShaderMaterial });
