import { useEffect } from "react";

const useScroll = (callback) => {
  const resizeHandler = (event) => 
    callback(event);
  
  // Add event listeners
  useEffect(() => {
    window.addEventListener("wheel", resizeHandler);
    // Remove event listeners on cleanup
    return () => {
      window.removeEventListener("wheel", resizeHandler);
    };
  });
};

export default useScroll;
