import React, { useRef } from "react";
import { useFrame } from "@react-three/fiber";
import { DoubleSide } from "three";
import FormatTexture from "../hooks/FormatTexture";
const Planet = ({
  heightMapSrc,
  AO,
  bumpScale,
  smokeScale,
  textureMapSrc,
  normalMapSrc,
  smoothMapSrc,
  metallicMapSrc,
  edgeMapSrc,
  args,
  animate,
  ...props
}) => {
  // not sure where to add this map will do rnd
  // const smoothMap = FormatTexture(smoothMapSrc);

  const heightMap = FormatTexture(heightMapSrc);
  const AoTextureMap = FormatTexture(AO);
  const textureMap = FormatTexture(textureMapSrc);
  const normalMap = FormatTexture(normalMapSrc);
  const metallicMap = FormatTexture(metallicMapSrc);
  const edgeMap = FormatTexture(edgeMapSrc);
  const TerrainMaterialRef = useRef();
  const AnimateFrame = ({ meshRef }) => {
    useFrame(({ clock }, delta) => {
      animate({ meshRef, clock, delta });
    });
  };
  return (
    <group {...props}>
      {animate && <AnimateFrame meshRef={TerrainMaterialRef} />}
      <mesh ref={TerrainMaterialRef} transparent={true}>
        <sphereGeometry
          attach="geometry"
          args={args}
          receiveShadow
          transparent={true}
        />
        <meshPhongMaterial
          bumpScale={bumpScale}
          attach="material"
          map={textureMap}
          aoMap={AoTextureMap}
          bumpMap={heightMap}
          lightMap={edgeMap}
          metalnessMap={metallicMap}
          normalMap={normalMap}
          side={DoubleSide}
        />
      </mesh>
    </group>
  );
};
export default Planet
;
