import { useState, useEffect } from "react";

export default function useMouseMoving(){
  const [mousePosition, setMousePosition] = useState({
    x: 0,
    y:0,
  });

  const getMouseEvent = (event) => {
    setMousePosition({
      x: event.clientX,
      y: event.clientY,
    });
  };

  // Add event listeners
  useEffect(() => {
    window.addEventListener("mousemove", getMouseEvent);
    // Remove event listeners on cleanup
    return () => {
      window.removeEventListener("mousemove", getMouseEvent);
    };
  }, []);

  return mousePosition;
};
