import React, { useEffect, useMemo, useCallback, useRef } from "react";
import { returnArray } from "../../utils";
import state from "../../store";
import { useSnapshot } from "valtio";
import { useKeyPress } from "../../hooks";
import {
  GiDeathStar,
  GiFlamingSheet,
  GiFission,
  GiSpaceship,
} from "react-icons/gi";
export default function SpaceInvaders() {
  const snap = useSnapshot(state);
  const width = snap.spaceInvasion.width;
  let alienIntervalId = useRef(),
    dir = useRef(1),
    gameTime = useRef(75),
    squares = returnArray(snap.spaceInvasion.gridSize);
  const mapAssets = (num) => {
    if (snap.spaceInvasion.hits.includes(num)) {
      return (
        <div
          className="inline-flex items-center justify-center w-[20px] h-[20px]"
          key={num}
        >
          <div className="boom w-[10px] h-[10px] text-amber-500 z-20">
            <GiFission />
          </div>
        </div>
      );
    }

    if (snap.spaceInvasion.aliens.includes(snap.spaceInvasion.player)) {
      return (
        <div className="bg-gray-400 rounded-full w-[20px] h-[20px]" key={num} />
      );
    }
    if (snap.spaceInvasion.aliens.includes(num)) {
      return (
        <div
          className="text-purple-600 rounded-full w-[20px] h-[20px]"
          key={num}
        >
          <GiDeathStar />
        </div>
      );
    }
    if (
      snap.spaceInvasion.shots.includes(num) &&
      snap.spaceInvasion.player === num
    ) {
      return (
        <div
          className="inline-flex items-center justify-center  text-gradO w-[20px] h-[20px] relative"
          key={num}
        >
          {" "}
          <GiSpaceship />
          <div className="rounder-full w-[10px] h-[10px] text-amber-400 rotate-[225deg] absolute bottom-full">
            <GiFlamingSheet className="w-full h-full" />
          </div>
        </div>
      );
    }
    if (snap.spaceInvasion.shots.includes(num)) {
      return (
        <div
          className="inline-flex items-center justify-center w-[20px] h-[20px]"
          key={num}
        >
          <div className="rounder-full w-[10px] h-[10px] text-amber-500 rotate-[225deg]">
            <GiFlamingSheet className="w-full h-full" />
          </div>
        </div>
      );
    }
    if (snap.spaceInvasion.player === num) {
      return (
        <div className="w-[20px] h-[20px] text-gradO" key={num}>
          <GiSpaceship />
        </div>
      );
    }
    return <div className="w-[20px] h-[20px] " key={num}></div>;
  };
  const leftArrow = useKeyPress("ArrowLeft");
  const rightArrow = useKeyPress("ArrowRight");
  const upArrow = useKeyPress("ArrowUp");
  const gameOver = useCallback(() => {
    snap.spaceInvasion.aliens.length > 0
      ? (state.spaceInvasion.status = false)
      : (state.spaceInvasion.status = true);
    state.spaceInvasion.gameOver = true;
  }, [snap]);
  useMemo(() => {
    if (!snap.spaceInvasion.pause) {
      leftArrow &&
        (() => {
          if (snap.spaceInvasion.player % width !== 0)
            state.spaceInvasion.player = snap.spaceInvasion.player - 1;
        })();
      rightArrow &&
        (() => {
          if (snap.spaceInvasion.player % width < width - 1)
            state.spaceInvasion.player = snap.spaceInvasion.player + 1;
        })();
      upArrow &&
        (() => {
          state.spaceInvasion.shots.push(snap.spaceInvasion.player);
        })();
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [leftArrow, rightArrow, upArrow]);

  const moveInvaders = useCallback(() => {
    const leftBoundry = snap.spaceInvasion.aliens[0] % width === 0,
      rightBoundry =
        snap.spaceInvasion.aliens[snap.spaceInvasion.aliens.length - 1] %
          width ===
        width - 1;
    if (!snap.spaceInvasion.pause) {
      if (snap.spaceInvasion.aliens.length === 0) gameOver();
      for (let i = 0; i < snap.spaceInvasion.aliens.length; i++) {
        if (snap.spaceInvasion.aliens[i] > squares.length - width) {
          console.log({ i }, snap.spaceInvasion.aliens[i], squares.length);
          gameOver();
          return;
        }
      }
      if (rightBoundry && dir.current > 0) {
        for (let i = 0; i < snap.spaceInvasion.aliens.length; i++) {
          state.spaceInvasion.aliens[i] += width + 1;
        }
        dir.current = -1;
      }
      if (leftBoundry && dir.current < 0) {
        for (let i = 0; i < snap.spaceInvasion.aliens.length; i++) {
          state.spaceInvasion.aliens[i] += width - 1;
        }
        dir.current = 1;
      }
      for (let i = 0; i < snap.spaceInvasion.aliens.length; i++) {
        state.spaceInvasion.aliens[i] += dir.current;
      }
      if (snap.spaceInvasion.aliens.includes(snap.spaceInvasion.player)) {
        gameOver();
        return;
      }
    }
  }, [snap, dir, squares, gameOver, width]);
  const moverShots = useCallback(() => {
    if (!snap.spaceInvasion.pause) {
      if (snap.spaceInvasion.shots.length > 0) {
        for (let w = 0; w < snap.spaceInvasion.shots.length; w++) {
          state.spaceInvasion.shots[w] -= width;
          let position = snap.spaceInvasion.shots[w];
          if (position < -width || isNaN(position)) {
            state.spaceInvasion.shots = snap.spaceInvasion.shots.filter(
              (num) => num > 0 && !isNaN(num) && num
            );
          }
          if (snap.spaceInvasion.aliens.includes(position)) {
            // state.spaceInvasion.pause = true;
            state.spaceInvasion.hits.push(position);
            state.spaceInvasion.aliens = snap.spaceInvasion.aliens.filter(
              (num) => num !== position && num
            );
            state.spaceInvasion.shots = snap.spaceInvasion.shots.filter(
              (num) => num !== position && num
            );
          }
        }
      }
    }
  }, [snap, width]);

  const playgame = useCallback(() => {
    moveInvaders();
    moverShots();
  }, [moveInvaders, moverShots]);
  useEffect(() => {
    let loaded = true;
    if (loaded && !snap.spaceInvasion.gameOver && !snap.spaceInvasion.pause) {
      alienIntervalId.current = setInterval(playgame, gameTime.current);
    }
    return () => {
      loaded = false;
      clearInterval(alienIntervalId.current);
    };
  }, [playgame, gameTime, alienIntervalId, snap]);

  return (
    <div className="my-5 w-[300px] flex flex-wrap border-black relative">
      {squares.map(mapAssets)}
    </div>
  );
}
