import React, { Suspense, useMemo } from "react";
import { Loader, CameraShake, Stars } from "@react-three/drei";
import { useSpring, animated } from "@react-spring/three";
import { Canvas } from "@react-three/fiber";
import { space } from "../utils";
import { TextureOptions } from "../hooks";
import { LightMap } from "../three/LightMap";
import * as THREE from "three";
import Ufo from "../three/Ufo";
import state from "../store";
import { useSnapshot } from "valtio";
import Planet from "../three/planet";
import CreateText from "../three/CreateText";
import { useResize } from "../hooks";
export default function SpaceInvadersCanvas() {
  const snap = useSnapshot(state);
  const props = {
    intensity: 5,
    ambient: 3,
    radius: 0,
    blend: 200,
  };
  const { width } = useResize();
  const isMobile = useMemo(() => width > "1440", [width]);
  const springs = useSpring({
    won:
      snap.spaceInvasion.status && snap.spaceInvasion.gameOver
        ? [space(-1.5), space(0.5), 0]
        : [-space(0.5), space(10), space(1)],
    lost:
      !snap.spaceInvasion.status && snap.spaceInvasion.gameOver
        ? [space(-1.5), space(0.5), 0]
        : [-space(0.5), -space(10), space(1)],
    ufo: isMobile
      ? [-space(2), space(0.05), space(-0)]
      : [-space(0), -space(0), space(0)],
  });
  const textOptions = {
    fontSize: !isMobile ? 0.2 : 1,
    fontHeight: !isMobile ? 0.1 : 0.3,
    position:!isMobile ?[space(1.2), space(.4), 0] : [space(3), 0, space(-2.8)],
    rotation:!isMobile ? [0, 0, 0]: [space(0), space(-.4), 0],
    color: "#fff",
  };
  return (
      <Suspense fallback={<Loader />}>
        <Canvas
          className="relative z-20 moon"
          performance={{ min: 0.5 }}
          onCreated={({ gl }) => {
            gl.toneMapping = THREE.CineonToneMapping;
            gl.setClearColor(new THREE.Color("#020207"));
          }}
          dpr={[1, 1.5]}
          gl={{ alpha: true }}
          flat
          linear
          shadows
        >
          <fog color="lightgray" attach="fog" near={4} far={800} />
          <LightMap {...props}>
            <Stars />
            <animated.group position={springs.ufo}>
              <Ufo
                position={[0, 0, 0]}
                rotation={[space(-0.3), space(0), space(0)]}
                animate={({ meshRef, clock, delta }) => {
                  meshRef.current.rotation.y += 0.01351;
                  meshRef.current.rotation.x =
                    Math.sin(performance.now() * 0.00045) * 0.1;
                  meshRef.current.rotation.z =
                    Math.cos(performance.now() * 0.00045) * 0.1;
                }}
              />
            </animated.group>

            <Planet
              bumpScale={0.3}
              args={[10, 10, 96]}
              position={[0, space(8), space(-20)]}
              {...TextureOptions("textures/earth")}
              animate={({ meshRef, delta, clock }) => {
                if (meshRef?.current) {
                  meshRef.current.rotation.set(
                    0,
                    space(clock.oldTime / 90000),
                    0
                  );
                }
              }}
            />
            {snap.spaceInvasion.status && snap.spaceInvasion.gameOver && (
              <animated.group position={springs.won}>
                <CreateText textstring="You Won" {...textOptions} />
              </animated.group>
            )}
            {!snap.spaceInvasion.status && snap.spaceInvasion.gameOver && (
              <animated.group position={springs.lost}>
                <CreateText textstring="You Lose" {...textOptions} />
              </animated.group>
            )}
            {!snap.thought && (
              <CameraShake
                maxYaw={0.01}
                maxPitch={0.1}
                maxRoll={0.1}
                yawFrequency={0.05}
                pitchFrequency={0.05}
                rollFrequency={0.04}
              />
            )}
          </LightMap>
        </Canvas>
      </Suspense>
  );
}
