import state from "../../../store";
import { useSnapshot } from "valtio";
import GlassButton from "../../../components/GlassButton";
import { ImArrowLeft, ImArrowRight } from "react-icons/im";
import { GiFlamingSheet } from "react-icons/gi";
export default function MobileButtons() {
  const snap = useSnapshot(state);
  return (
    <section className="bg-white/5 rounded-xl relative flex">
      <GlassButton
        onClick={() => {
          if (snap.spaceInvasion.player % snap.spaceInvasion.width !== 0)
            state.spaceInvasion.player = snap.spaceInvasion.player - 1;
        }}
      >
        <span className="mobile_btn">
          <ImArrowLeft className="mobile_btn_icon" />
        </span>
      </GlassButton>
      <GlassButton
        onClick={() => {
          state.spaceInvasion.shots.push(snap.spaceInvasion.player);
        }}
      >
        <span className="mobile_btn">
          <GiFlamingSheet className="mobile_btn_icon rotate-[225deg]" />
        </span>
      </GlassButton>
      <GlassButton
        onClick={() => {
          if (
            snap.spaceInvasion.player % snap.spaceInvasion.width <
            snap.spaceInvasion.width - 1
          )
            state.spaceInvasion.player = snap.spaceInvasion.player + 1;
        }}
      >
        <span className="mobile_btn">
          <ImArrowRight className="mobile_btn_icon" />
        </span>
      </GlassButton>
    </section>
  );
}
