import * as THREE from "three";
import { extend } from "@react-three/fiber";

class RainMaterial extends THREE.ShaderMaterial {
  constructor(texture) {
    super({
      vertexShader: `varying vec2 vUv;
      void main() {
        vUv = uv;
        gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
      }`,
      fragmentShader: `
      uniform vec3 u_resolution;
      uniform float u_time;

      #define TAU 6.28318530718
      #define MAX_ITER 5

      void mainImage( out vec4 fragColor, in vec2 fragCoord ) 
      {
        float time = u_time * .5+23.0;
        // uv should be the 0-1 uv of texture...
      vec2 uv = fragCoord.xy / u_resolution.xy;
        
    #ifdef SHOW_TILING
      vec2 p = mod(uv*TAU*2.0, TAU)-250.0;
    #else
        vec2 p = mod(uv*TAU, TAU)-250.0;
    #endif
      vec2 i = vec2(p);
      float c = 1.0;
      float inten = .005;
    
      for (int n = 0; n < MAX_ITER; n++) 
      {
        float t = time * (1.0 - (3.5 / float(n+1)));
        i = p + vec2(cos(t - i.x) + sin(t + i.y), sin(t - i.y) + cos(t + i.x));
        c += 1.0/length(vec2(p.x / (sin(i.x+t)/inten),p.y / (cos(i.y+t)/inten)));
      }
      c /= float(MAX_ITER);
      c = 1.17-pow(c, 1.4);
      vec3 colour = vec3(pow(abs(c), 8.0));
        colour = clamp(colour + vec3(0.0, 0.35, 0.5), 0.0, 1.0);
    
      #ifdef SHOW_TILING
      // Flash tile borders...
      vec2 pixel = 2.0 / u_resolution.xy;
      uv *= 2.0;
      float f = floor(mod(u_time*.5, 2.0)); 	// Flash value.
      vec2 first = step(pixel, uv) * f;		   	// Rule out first screen pixels and flash.
      uv  = step(fract(uv), pixel);				// Add one line of pixels per tile.
      colour = mix(colour, vec3(1.0, 1.0, 0.0), (uv.x + uv.y) * first.x * first.y); // Yellow line
      #endif
        
      fragColor = vec4(colour, 1.0);
      }
    varying vec2 vUv;
            void main() {
                mainImage(gl_FragColor, vUv * u_resolution.xy);
              }
    
        `,
      uniforms: {
        iChannel0: { value: texture },
        u_time: { type: "f", value: 0.05 },
        u_speed: { type: "f", value: performance.now() },
        u_mouse: { type: "f", value: { x: 0, y: 0 } },
        u_resolution: {
          type: "v2",
          value: new THREE.Vector2(window.innerWidth, window.innerWidth),
        },
      },
      transparent: true,
      blending: THREE.NormalBlending,
      depthWrite: false,
    });
  }
}

extend({ RainMaterial });
