import * as THREE from "three";
import { extend } from "@react-three/fiber";

class SquareShaderMaterial extends THREE.ShaderMaterial {
  constructor(texture) {
    super({
      vertexShader: `varying vec2 vUv;
      void main() {
        vUv = uv;
        gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
      }`,
      fragmentShader: `#include <common>

      uniform vec3 u_resolution;
      uniform float u_time;
      uniform sampler2D iChannel0;
    
      // By Daedelus: https://www.shadertoy.com/user/Daedelus
      // license: Creative Commons Attribution-NonCommercial-ShareAlike 3.0 Unported License.
      #define TIMESCALE 0.25 
      #define TILES 8
      #define COLOR 0.7, 1.6, 2.8
    
      void mainImage( out vec4 fragColor, in vec2 fragCoord )
      {
        float aspect_ratio = u_resolution.y/u_resolution.x;
        vec2 uv = fragCoord.xy / u_resolution.x;
        uv -= vec2(0.5, 0.5 * aspect_ratio);
        float rot = radians(-30. -u_time); // radians(45.0*sin(u_time));
        mat2 rotation_matrix = mat2(cos(rot), -sin(rot), sin(rot), cos(rot));
        uv = rotation_matrix * uv;
        vec2 scaled_uv = 20.0 * uv; 
        vec2 tile = fract(scaled_uv);
        float tile_dist = min(min(tile.x, 1.0-tile.x), min(tile.y, 1.0-tile.y));
        float square_dist = length(floor(scaled_uv));
        
        float edge = sin(u_time-square_dist*20.);
        edge = mod(edge * edge, edge / edge);

        float value = mix(tile_dist, 1.0-tile_dist, step(1.0, edge));
        edge = pow(abs(1.0-edge), 2.2) * 0.5;
        
        value = smoothstep( edge-0.05, edge, 0.95*value);
        
        
        value += square_dist*.1;
        value *= 0.8 - 0.2;
        fragColor = vec4(pow(value, 2.), pow(value, 1.5), pow(value, 1.2), 1.);
      }
    
      varying vec2 vUv;
    
      void main() {
        mainImage(gl_FragColor, vUv * u_resolution.xy);
      }
      `,
      uniforms: {
        iChannel0: { value: texture },
        u_time: { type: "f", value: 0.05 },
        u_speed: { type: "f", value: performance.now() },
        u_mouse: { type: "f", value: { x: 0, y: 0 } },
        u_resolution: {
          type: "v2",
          value: new THREE.Vector2(window.innerWidth, window.innerWidth),
        },
      },
      transparent: true,
      blending: THREE.NormalBlending,
      depthWrite: false,
    });
  }
}

extend({  SquareShaderMaterial });
